/* Modal overlay */
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  /* Modal content styling */
  .modal-content {
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    width: 400px;
    max-width: 90%;
    max-height: 80%;  /* Allow scrolling if content is too tall */
    overflow-y: auto; /* Enable scrolling */
    display: flex;
    flex-direction: column;
  }
  
  h2 {
    font-size: 1.5rem;
    margin-bottom: 20px;
    text-align: center;
  }
  
  button.close-button {
    background-color: transparent;
    color: #000;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
    align-self: flex-end;
  }
  
  button.close-button:hover {
    color: red;
  }
  
  .client-details {
    font-size: 1rem;
  }
  
  .client-details p {
    margin: 5px 0;
  }
  
  .message, .timeline-event, .recommendation {
    margin-bottom: 10px;
  }
  