/* MealItem.css */

/* Overall meal card */
.meal-item {
    background-color: #f0f0f0;
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    margin-bottom: 10px;
  }
  
  /* Each category */
  .meal-category {
    margin-bottom: 5px; /* Reduced space between categories */
  }
  
  /* Category title (only shown when the category has food items) */
  .meal-category h4 {
    display: block;
    font-size: 1.1rem;
    margin-bottom: 5px;
  }
  
  /* Food list styling */
  .meal-category > ul {
    list-style-type: none;
    padding-left: 0;
  }
  
  .meal-category > ul > li {
    margin-bottom: 3px;
    font-size: 0.9rem;
  }
  
  .meal-item h3 {
    font-size: 1.5rem;
    margin-bottom: 10px;
  }
  
  .meal-item p {
    margin: 5px 0;
  }
  .food-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 0;
  }
  
  .remove-food-button {
    margin-left: 10px;
    padding: 5px 10px;
    background-color: #e53935;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 12px;
  }
  
  .remove-food-button:hover {
    background-color: #d32f2f;
  }
  