/* Workspace */
.workspace {
  padding: 20px;
  background-color: #f1f8e9; /* Light green */
  border-radius: 8px;
}

.workspace h2 {
  font-size: 1.8rem;
  margin-bottom: 20px;
}

/* Workspace Panels */
.workspace-panels {
  display: flex;
  gap: 20px;
  flex-wrap: wrap; /* Ensure cards wrap on smaller screens */
}

.workspace-panel {
  background-color: #ffffff;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-sizing: border-box;
  width: 30%;
  height: 250px;
  cursor: pointer;
}

.workspace-panel:hover {
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
}

/* Specific panel styling */
.workspace-panel.menu-creation {
  background-color: #fff3e0; /* Light orange */
}

.workspace-panel.menu-storage {
  background-color: #e3f2fd; /* Light blue */
}

/* Create New Menu Button */
.create-menu-button-container {
  margin-top: 20px;
  text-align: center;
}

.create-menu-button {
  background-color: #ff9800; /* Orange */
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 5px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.create-menu-button:hover {
  background-color: #f57c00; /* Darker orange */
}
