.food-input-form {
    background-color: #ffffff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .food-input-form label {
    display: block;
    font-weight: bold;
    margin-bottom: 8px;
  }
  
  .food-input-form select,
  .food-input-form input {
    width: 100%;
    padding: 8px;
    margin-bottom: 10px;
  }
  
  .add-food-button {
    background-color: #4caf50;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .add-food-button:hover {
    background-color: #388e3c;
  }
  