.meal-select {
    padding: 20px;
    background-color: #ffffff;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .meal-select label {
    display: block;
    font-weight: bold;
    margin-bottom: 8px;
  }
  
  .meal-select select {
    width: 100%;
    padding: 8px;
    margin-bottom: 10px;
  }
  
  .add-meal-button {
    background-color: #4caf50;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .add-meal-button:hover {
    background-color: #388e3c;
  }
  